<template>
    <div>
      <h1>Topics</h1>
      <input type="text" v-model="search" placeholder="Search...">
      <ul>
        <li v-for="item in filteredItems" :key="item.id">
          <h2>{{ item.value }}</h2>
          <p>{{ item.sourceNote }}</p>
        </li>
      </ul>
    </div>
  </template>

  <script>
  import axios from 'axios';

  export default {
    perPage: 3,
    currentPage: 1,
    name: 'TopicsData',
    data() {
      return {
        items: [],
        pageOfItems: [],
        search: "",
        filter: "",
      };
    },
    computed: {
      filteredItems() {
        return this.items.filter((item) => {
          return (
            item.value.toLowerCase().includes(this.search.toLowerCase()) ||
            item.sourceNote.toLowerCase().includes(this.search.toLowerCase()) &&
            (this.filter === "")
          );
        });
      },
    },
    mounted() {
      axios
      .get('https://52.139.170.67:5000/topic')
      .then(response => {
        response.data.forEach(element => {
          this.items.push({id: element[2], value: element[1], sourceNote: element[0]});
        });
      });
    },
    methods: {
        onChangePage(pageOfItems) {
            this.pageOfItems = pageOfItems;
        }
    }
  };
  </script>

